/**
 * @author gyb(mocheer)
 * @email `mocheer@foxmail.com`  
 * @since 2017.6.12
 */
/**
 *  @param {boolean} restart 
 */
export default class Tool {
  constructor(owner, options) {
    this.owner = owner;
    this.dmap = owner.dmap;
    this.map = owner.map;
    this.layer = owner.layer;
    this.data = options.data; //layeridid
  }
  /**
   * 
   */
  initMouseDownHandler() {
    this.onMouseDown = this.onMouseDown || (e => {
      if (e.originalEvent.button == 0) {
        this._onMouseDown(e);
      }
    })
  }
  /**
   * 
   * @param options 
   */
  setOptions(options) {
    this.options = T.assign(this.options || {}, options);
  }
  /**
   * 
   */
  reback() {
    let {
      marker,
      layer,
      history
    } = this;
    if (marker) {
      if (marker._defaultShape) { ///
        let latlngs = marker._defaultShape();
        let latlng = latlngs.pop();
        if (latlng) {
          history.push(latlng);
          marker.setLatLngs(latlngs);
        } else {
          marker.removeFrom(layer);
          return true;
        }
      } else {
        marker.removeFrom(layer);
        return true;
      }
    }
  }
  /**
   * 
   */
  restore() {
    let {
      marker,
      layer,
      history
    } = this;
    if (marker) {
      marker.addTo(layer);
      //
      if (marker._defaultShape && history.length > 0) { ///
        let latlngs = marker._defaultShape();
        let latlng = history.pop();
        latlngs.push(latlng);
        marker.setLatLngs(latlngs);
      } else {
        return true;
      }
    }
  }
  /**
   * 
   * @param e 
   */
  onEnd(e) {
    //
    this.history = [];
    //
    this.dmap.do('draw-end', {
      target: this,
      marker: this.marker
    });
  }
  /**
   * 
   */
  clear() {
    let {
      marker
    } = this;
    marker && marker.remove();
  }
  /**
   * 
   */
  reset() {
    if (this.restart) {
      this.clear();
      this.restart = false;
      this.marker = null;
    }
  }
  /**
   * 
   */
  cancel() {

  }
  /**
   * 
   */
  clone() {
    let tool = new this.constructor(this.owner, this.options);
    tool.options = this.options;
    return tool;
  }
}



